import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../../Validate/Validator';
import Input from '../../redux-form/Input';
import s from './Messeges.module.css';

let newArray = [];
const maxLength50 = maxLengthCreator(50);

const Messeges = (props) => {
  let onSubmit = (formData) => {
    
    let text = formData.message;
    let action = { type: 'addSms' , text};
    props.addSmsContainer(action)
  }
  
  return (
    <div>
      <div className={s.messagesBlock}>
        {newArray = props.dialogsPage.messages.map(m => <div>{m.id} {m.message}</div>)}
      </div>
      <div className='flex ' >
        <DialogMessageFormRedux onSubmit={onSubmit}/>
        
      </div>
      
    </div>
  )
}

const DialogMessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit} className={s.textSms} >
        <div>
            <Field validate={[ required,maxLength50]} className={s.textSms}  component={Input} name='message' placeholder='write your message' />
            <button className={s.addSms} >add sms</button>
        </div>
        
    </form>
  )
}
const DialogMessageFormRedux = reduxForm({form:'DialogMessageForm'})(DialogMessageForm);


export default Messeges;