let startData={
        frends: [
            { id: 1, name: 'Alex' },
            { id: 2, name: 'Male' },
            { id: 3, name: 'Forck' },
            { id: 4, name: 'Nock' },
            { id: 5, name: 'Morsi' },
            { id: 6, name: 'Gargo' },
        ],
        messages: [
            { id: 1, message: 'hi' },
            { id: 2, message: 'fo' },
        ],
    
};


const dialogsReduser = (state=startData,action)=>{
    switch(action.type){
        case 'addSms':{
            let newSms = {id:state.messages.length+1,message:action.text};
            return {
                ...state,
                messages : [...state.messages, newSms],
            }
        }
        
            
    }
    return state;
}


export default dialogsReduser;