import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Frend.module.css';

const Frend = (props) => {
  return (
    <div className={s.blockFrend}>
      <NavLink className={s.a} to={'/dialogs/'+props.id}>{props.name}</NavLink>
    </div>
  );
}

export default Frend;