import { stopSubmit } from 'redux-form';
import {getAuthMe, login, logout} from '../Dal/Api'

let startData = {
    id: null,
    login: null,
    email: null
}

const authReduser = (state = startData, action) => {
    switch (action.type) {
        case 'setAuthData':
            return {
                
                ...state,
                ...action.data
            }
        default:
            return state;

    }
}




export const setAuthData = (id,login,email) => ({ type: 'setAuthData', data:{id,login,email} });

export const getAuthMeTC = (info) => {
    return (dispatch) =>{
        getAuthMe().then(data => {
            data.resultCode === 0 
            ? dispatch(setAuthData(data.data.id,data.data.login,data.data.email))
            : dispatch(setAuthData(info,info,info))
          })
    }
};
export const loginTC = (email,password,rememberMe) => {
    return (dispatch) =>{
        login(email,password,rememberMe).then(data => {
            data.resultCode === 0 
            ? dispatch(getAuthMeTC()) 
            : dispatch(stopSubmit('login',{'_error':data.messages}));
          })
    }
};
export const logoutTC = () => {
    return (dispatch) =>{
        logout().then(data => {
            data.resultCode === 0 ? dispatch(getAuthMeTC(false)) : alert(data.messages);
          })
    }
};

export default authReduser;